<style lang="less">
  @import '../../styles/common.less';
  @import 'components/table.less';

  p {
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
  }
</style>

<template>
  <div>
    <Row>
      <Col span="6">
        <Card>
          <p slot="title">
            <Icon type="ios-redo"></Icon>
            选择数据库
          </p>
          <div class="edittable-test-con">
            <Form :model="formItem" :label-width="100" ref="formItem" :rules="ruleValidate">
              <Form-item label="环境:" prop="computer_room">
                <Select v-model="formItem.computer_room" placeholder="请选择" @on-change="Connection_Name">
                  <Option v-for="i in dataset" :value="i" :key="i">{{ i }}</Option>
                </Select>
              </Form-item>
              <Form-item label="业务信息:" prop="connection_name">
                <Select v-model="formItem.connection_name" placeholder="请选择" @on-change="DataBaseName">
                  <Option v-for="i in tableform.sqlname" :value="i.connection_name" :key="i.connection_name" filterable>
                    {{ i.connection_name }}
                  </Option>
                </Select>
              </Form-item>
              <Form-item label="数据库库名:" prop="basename">
                <Select v-model="formItem.basename" placeholder="请选择" @on-change="GetTableName" filterable>
                  <Option v-for="item in tableform.basename" :value="item" :key="item">{{ item }}</Option>
                </Select>
              </Form-item>
              <Form-item label="数据库表名:" prop="tablename">
                <Select v-model="formItem.tablename" placeholder="请选择" filterable>
                  <Option v-for="item in tableform.info" :value="item" :key="item">{{ item }}</Option>
                </Select>
              </Form-item>
              <Button type="warning" @click="canel()" style="margin-left: 20%">重置</Button>
              <Button type="primary" @click="getinfo()" style="margin-left: 5%">连接</Button>
            </Form>
            <br>
            <Tabs value="order1" style="height: 300px;overflow-y: scroll;">
              <TabPane label="生成语句" name="order1">
                <p v-for="list in sql" style="font-size: 12px;color:#2b85e4"> {{ list }}<br><br></p>
              </TabPane>
              <TabPane label="提交工单" name="order2">
                <Button type="primary" style="margin-left: 25%;margin-top: 20%;" @click.native="orderswitch"
                        size="large">获取工单详情
                </Button>
              </TabPane>
            </Tabs>
          </div>
        </Card>
      </Col>
      <Col span="18" class="padding-left-10">
        <Card>
          <p slot="title">
            <Icon type="md-remove"></Icon>
            表结构详情
          </p>
          <div class="edittable-table-height-con">
            <Tabs :value="tabs">
              <TabPane label="表字段详情" name="order1" icon="md-apps">
                <Table :columns="tabcolumns" :data="TableDataNew"></Table>
              </TabPane>
              <TabPane label="添加&删除索引" name="order2" icon="md-add">
                <editindex :tabledata="indexinfo" :table_name="formItem.tablename"
                           @on-indexdata="getindexconfirm"></editindex>
                <br>
                <br>
                <br>
                <br>
              </TabPane>
            </Tabs>
          </div>
        </Card>
      </Col>
    </Row>

    <Modal v-model="openswitch" @on-ok="commitorder" :ok-text="'提交工单'" width="800">
      <Row>
        <br>
        <Card>
          <div class="step-header-con">
            <h3>比心数据库自动化平台</h3>
            <Divider />
          </div>
          <p class="step-content"></p>
          <Form class="step-form" :label-width="100">
            <FormItem label="用户名:">
              <p>{{username}}</p>
            </FormItem>
            <FormItem label="数据库库名:">
              <p>{{formItem.basename}}</p>
            </FormItem>
            <FormItem label="数据库表名:">
              <p>{{formItem.tablename}}</p>
            </FormItem>
            <FormItem label="执行SQL:">
              <p v-for="i in sql">{{i}}</p>
            </FormItem>
            <FormItem label="工单提交说明:" required>
              <Input v-model="formItem.text" placeholder="请输入工单说明"></Input>
            </FormItem>
            <FormItem label="指定审核人:" required>
              <Select v-model="formItem.assigned" filterable transfer>
                <Option v-for="i in this.assigned" :value="i" :key="i">{{i}}</Option>
              </Select>
            </FormItem>
            <FormItem label="延迟执行">
              <InputNumber
                v-model="formItem.delay"
                :formatter="value => `${value}分钟`"
                :parser="value => value.replace('分钟', '')"
                :min="0">
              </InputNumber>
            </FormItem>
            <FormItem label="是否备份">
              <RadioGroup v-model="formItem.backup">
                <Radio label="1">是</Radio>
                <Radio label="0">否</Radio>
              </RadioGroup>
            </FormItem>
            <FormItem label="确认提交：" required>
              <Checkbox v-model="pass">确认</Checkbox>
            </FormItem>
          </Form>
        </Card>
      </Row>
    </Modal>
  </div>
</template>

<script>
  //
  import axios from 'axios'
  import util from '../../libs/util'
  import editindex from './components/modifyIndex.vue'

  export default {
    components: {
      editindex
    },
    data () {
      return {
        ruleValidate: {
          computer_room: [{
            required: true,
            message: '环境地址不得为空',
            trigger: 'change'
          }],
          connection_name: [{
            required: true,
            message: '业务信息不得为空',
            trigger: 'change'
          }],
          basename: [{
            required: true,
            message: '数据库名不得为空',
            trigger: 'change'
          }],
          tablename: [{
            required: true,
            message: '表名不得为空',
            trigger: 'change'
          }],
          text: [{
            required: true,
            message: '说明不得为空',
            trigger: 'change'
          },
            {
              type: 'string',
              max: 20,
              message: '最多20个字',
              trigger: 'blur'
            }
          ]
        },
        dataset: [],
        item: {},
        basename: [],
        sqlname: [],
        TableDataNew: [],
        tableform: {
          sqlname: [],
          basename: [],
          info: []
        },
        tabcolumns: [
          {
            title: '字段名',
            key: 'Field'
          },
          {
            title: '字段类型',
            key: 'Type',
            editable: true
          },
          {
            title: '字段是否为空',
            key: 'Null',
            editable: true,
            option: true
          },
          {
            title: '默认值',
            key: 'Default',
            editable: true
          },
          {
            title: '索引类型',
            key: 'Key'
          },
          {
            title: '备注',
            key: 'Extra'
          }
        ],
        username: sessionStorage.getItem('user'),
        indexinfo: [],
        sql: [],
        openswitch: false,
        pass: false,
        formItem: {
          text: '',
          computer_room: '',
          connection_name: '',
          basename: '',
          tablename: '',
          backup: '0',
          assigned: '',
          delay: 0
        },
        id: null,
        tabs: 'order1',
        assigned: []
      }
    },
    methods: {
      Connection_Name (index) {
        if (index) {
          this.ScreenConnection(index)
        }
      },
      DataBaseName (index) {
        if (index) {
          this.id = this.item.filter(item => {
            if (item.connection_name === index) {
              return item
            }
          })
          axios.put(`${util.url}/workorder/basename`, {
            'id': this.id[0].id
          })
            .then(res => {
              this.tableform.basename = res.data
            })
            .catch(() => {
              util.err_notice('无法连接数据库!请检查网络')
            })
        }
      },
      ScreenConnection (b) {
        this.tableform.sqlname = this.item.filter(item => {
          if (item.computer_room === b) {
            return item
          }
        })
      },
      GetTableName () {
        if (this.formItem.basename) {
          let data = JSON.stringify(this.formItem)
          axios.put(`${util.url}/workorder/tablename`, {
            'data': data,
            'id': this.id[0].id
          })
            .then(res => {
              this.tableform.info = res.data
            }).catch(error => {
            util.err_notice(error)
          })
        }
      },
      getdatabases () {
        axios.put(`${util.url}/workorder/connection`, {'permissions_type': 'ddl'})
          .then(res => {
            this.item = res.data['connection']
            this.assigned = res.data['assigend']
            this.dataset = res.data['custom']
          })
          .catch(error => {
            util.err_notice(error)
          })
      },
      getinfo () {
        this.$refs['formItem'].validate((valid) => {
          if (valid) {
            this.$Spin.show({
              render: (h) => {
                return h('div', [
                  h('Icon', {
                    props: {
                      size: 30,
                      type: 'ios-loading'
                    },
                    style: {
                      animation: 'ani-demo-spin 1s linear infinite'
                    }
                  }),
                  h('div', '数据库连接中,请稍后........')
                ])
              }
            })
            this.formItem.table_name = this.formItem.tablename
            axios.put(`${util.url}/workorder/field`, {
              'connection_info': JSON.stringify(this.formItem),
              'id': this.id[0].id
            })
              .then(res => {
                this.TableDataNew = res.data
                this.$Spin.hide()
              })
              .catch(() => {
                this.$Spin.hide()
                util.err_notice('连接失败！详细信息请查看日志')
              })
            this.getindex()
          } else {
            this.$Message.error('表单验证失败!')
          }
        })
      },
      canel () {
        this.sql = []
        this.pass = false
      },
      getindex () {
        if (this.formItem.table_name) {
          axios.put(`${util.url}/workorder/indexdata`, {
            'login': JSON.stringify(this.formItem),
            'table': this.formItem.tablename,
            'id': this.id[0].id
          })
            .then(res => {
              this.indexinfo = res.data
            }).catch(error => {
            util.err_notice(error)
          })
        }
      },
      getindexconfirm (val) {
        for (let i of val) {
          this.sql.push(i)
        }
      },
      orderswitch () {
        this.openswitch = !this.openswitch
      },
      commitorder () {
        if (this.sql === [] || this.formItem.basename === '' || this.assigned === '' || this.formItem.text === '' || this.formItem.assigned === '') {
          util.err_notice('工单数据缺失,请检查工单信息是否缺失!')
        } else {
          if (this.pass === true) {
            axios.post(`${util.url}/sqlsyntax/`, {
              'data': JSON.stringify(this.formItem),
              'sql': JSON.stringify(this.sql),
              'real_name': sessionStorage.getItem('real_name'),
              'type': 1,
              'id': this.id[0].id
            })
              .then(res => {
                util.notice(res.data)
                this.$router.push({
                  name: 'myorder'
                })
              }).catch(error => {
              util.err_notice(error)
            })
          } else {
            util.err_notice('提交工单需点击确认按钮')
          }
        }
      }
    },
    mounted () {
      this.getdatabases()
    }
  }
</script>



